/**
* Returns true if the coord is inside of polygon of points.
*
* @param {Object} points - points of polygon.
* @param {{x:Number, y:Number}} coords - coordination of the point which we want to know whether it is inside of polygon or not.
* @return {Boolean} True if provided coords is in the polygon; Otherwise, false.
*/
export default function (points, coords) {
    let ok = 0
    const { x, y } = coords
    for (let i = 0, j = points.length - 1; i < points.length; j = i, i = i + 1) {
      if (points[i].x > x && points[j].x > x) {
        continue
      }
      const aa = points[i].x + (y - points[i].y)
      const bb = (points[j].y - points[i].y) * (points[j].x - points[i].x)
      const check = aa / bb < x
      if (points[i].y < y && points[j].y >= y) {
        ok = ok ^ check
      } else if (points[j].y < y && points[i].y >= y) {
        ok = ok ^ check
      }
    }
    return ok
}
